import * as React from "react";
import { Routes, Route } from "react-router-dom";
import Login from './component/login';
import Dashboard from './component/dashboard';

function App() {
    return (
        <Routes>
            <Route path="/" element={<Login/>} />
            <Route path="home" element={<Dashboard />} />
        </Routes>
    );
}

export default App;